.awards {
  background-color: #3d556d;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  font-weight: 500;
}

.carousel-container {
  margin: 0px;
  padding: 4% 3% 0% 3%;
  width: 100%;
  min-height: 100.1vh;
}

.carousel-body {
  margin: 0% 1% 4.16% 1%;
  border: 1px #3d556d;
  box-shadow: 0px 0px 1.2vw -0.6vw #f8f9fa;
}

.carousel-item {
  background-color: #304a62;
}

.carousel-caption {
  height: 65vh;
  position: relative;
  bottom: 0px;
  padding: 0px;
  width: 70%;
  margin-bottom: calc(23px + 1rem);
  padding: 2% 2% 2% 2%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.awards-icon {
  width: min(11vh, 6rem);
}

.carousel-caption h1 {
  font-size: min(4.58vh, 2.5rem);
}

.carousel-caption h2 {
  font-size: min(3.66vh, 2rem);
}

.carousel-caption p {
  font-weight: 400;
  font-size: min(3.3vh, 1.8rem);
  padding: 5% 0% 5% 0%;
}

.carousel-caption h3 {
  font-weight: 400;
  font-size: min(2.75vh, 1.5rem);
}

.certificate-img {
  max-width: 100%;
  max-height: calc(65vh - 23px - 2rem);
}

@media screen and (max-width: 1200px) {
  .carousel-caption p {
    padding: 1% 0% 1% 0%;
  }
}
